/* Hide scrollbar for Chrome, Safari, and Opera */
::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for Firefox */
html {
  scrollbar-width: none;
  overflow: -moz-scrollbars-none;
}

/* Hide scrollbar for IE and Edge */
body {
  -ms-overflow-style: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.player-wrapper {
  width: 80%;
  margin: 20px auto;
  max-width: 800px;
}
@keyframes fadeOut {
  0%   { opacity: 1; display: block; }
  99%  { opacity: 0; display: block; }
  100% { opacity: 0; display: none; }
}
.App-header {
  
  padding: 10px 20px;  /* reduce padding */
  color: rgb(99, 97, 97);
  text-align: center;  /* center the header text */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  opacity: 1; /* Start with 90% opacity or any desired value */
  animation: fadeOut 2s forwards 1s; /* Start fade-out after 3 seconds, fade out over 1 second */
  pointer-events: none;
}

.epg-container{
  position: fixed;
  bottom: 5%;
  background-color: rgba(10,10,10,0.7);
  color: white;
  padding: 8px 16px 20px 0px;
  margin: 20px;
  left: 0;
  padding-left: 6em;  
}
.epg-container .epg-station-logo{
  float:left;
  position: absolute;
  width:5em;
  height:5em;
  left:2em;
  top:0;
}
.epg-program h4{
  padding: 0 0 0 2%;
  margin: 8px 0 0 0;
}
.epg-program .remaining-time{
  color:white;
  width: 8em;
}
.epg-program .program-progress{
  display: inline-flex;
  width: 40%;
  float: left;
  position: absolute;
  padding-left: 4em;
}
.program-title{
  text-align: left;  
}
.program-time{
  margin-right: 30px;
  float: inline-end; 
}
.program-desc{
  text-align:left;
  margin: 0 0 0 2%;
  font-size: 0.9em;
}
.video-player {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #1E1E1E;
}

.video-player video {
  width: 100%;
  height: auto;
  border: none;
  outline: none;
}